/*@import "compass/css3";*/

html,
body {
    background-color: rgb(1,12,13);
}

.planet {
    background-size: cover;
    left: 50%;
    position: absolute;
    top: 50%;
}

#background {
    background-image: url('http://www.alienresearchcorp.com/space/hd-wallpapers/amazing/img/l/moment-in-space-cvi.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    bottom: -20%;
    left: -10%;
    position: absolute;
    right: -10%;
    top: -20%;
}

#x-axis {
    bottom: -50%;
    left: 50%;
    top: -50%;
    width: 1px;
}

#y-axis {
    left: 0;
    top: 50%;
    right: 0;
    height: 1px;
}

