.MainPage_mainGrid__2_Axi {
    display: grid;
    grid-template-columns: 250px auto auto;
    grid-gap: 1em;
    margin: 150px auto 150px;
    background-color: white;
    max-width: 1000px;
    background-color:rgba(0, 0, 0, 0);
    z-index: 5;
    overflow: auto;
    position: relative;
}

