}

.MainPage_cardContainer__KZTuw {
    grid-column: 1 / 1;
    background-color: inherit;
    width: 250px;
    display: flex;
    flex-direction: column;
}

.MainPage_serverCard__3KeeB:not(:last-child) {
    margin-bottom: 20px;
}

.MainPage_newsContainer__3VlDM {
    grid-column: 2 / 4;
    background-color: gray;
}

@media (max-width: 768px) {
    .MainPage_mainGrid__2_Axi{
        margin: 0 auto 0;
    }
    .MainPage_cardContainer__KZTuw{
        display: none
    }
    .MainPage_newsContainer__3VlDM {
         grid-column: 1 / 4;
     }

}
